/** @return A json representation of our teammate. */
const createTeammate = function(name, description, hobby, askMeAbout) {
  return { name, description, hobby, askMeAbout };
};

/** Json of all teammates.. */
const teammates = {
  1: createTeammate(
    'Teammate A Name',
    'Feelings',
    'AspirationalHobby:',
    'Ask me About: '
  ),
  2: createTeammate(
    'Teammate B Name',
    'Feelings',
    'AspirationalHobby:',
    'Ask me About: '
  ),
  3: createTeammate(
    'Teammate C Name',
    'Feelings',
    'AspirationalHobby:',
    'Ask me About: '
  ),
  4: createTeammate(
    'Teammate D Name',
    'Feelings',
    'AspirationalHobby:',
    'Ask me About: '
  ),
  keys: [1, 2, 3, 4]
};

/**
 * A reducer that manipulates the state of team member info.
 * @param state The state of team members.
 */
const teammatesReducer = function(state = teammates) {
  return state;
};

export default teammatesReducer;
